import { IGoogleDriveRepository } from "../../Repositories/DataSources/GoogleDriveRepository"
import { PublicationModel } from "./PublicationModel"

export interface IPublicationRepository {
    fetchAllPublications(): Promise<PublicationModel[]>
    fetchPage(name: string): Promise<PublicationModel>
}

export class PublicationRepository implements IPublicationRepository {
    private repository: IGoogleDriveRepository

    constructor(repository: IGoogleDriveRepository) {
        this.repository = repository
    }

    async fetchPage(name: string): Promise<PublicationModel> {
        // @ts-ignore
        const pageContent = await this.repository.fileContent(process.env[`REACT_APP_${name.toUpperCase}`])

        return {
            content: pageContent
        }
    }

    async fetchAllPublications(): Promise<PublicationModel[]> {
        const filesList = await this.repository.listFiles(process.env.REACT_APP_PUBLICATIONS_DIR as string)
        console.log(filesList)

        return []
    }
}