import { Outlet } from "react-router-dom";
import NavBar from "../Components/NavBar/NavBar";
import FooterBar from "../Components/FooterBar/FooterBar";

export default function Home() {
    return (
        <div className="App">
            
            <header>
                <NavBar />
            </header>

            <main>
                <Outlet />
            </main>

            <footer >
                <FooterBar />
            </footer>

        </div>
    )
}