import { FileModel, ListFilesModel } from '../Models/GoogleDriveModel';

export interface IGoogleDriveRepository {
  listFiles(folderId: string): Promise<ListFilesModel>,
  fileStat(fileId: string, fields?: string[]): Promise<FileModel>,
  fileContent(fileId: string): Promise<string>
}

export class GoogleDriveRepository implements IGoogleDriveRepository {
  async listFiles(folderId: string): Promise<ListFilesModel> {
    const request = await fetch(`https://www.googleapis.com/drive/v3/files?q='${folderId}'+in+parents&fields=*&key=${process.env.apiKey}`);
    return await request.json();
  }
  async fileStat(fileId: string, fields?: string[]): Promise<FileModel> {
    const request = await fetch(`https://www.googleapis.com/drive/v3/files/${fileId}?key=${process.env.apiKey}${fields ? '&fields=' + fields.join(',') : ''}`)
    return await request.json()
  }
  async fileContent(fileId: string): Promise<string> {
    const request = await fetch(`https://www.googleapis.com/drive/v3/files/${fileId}?key=${process.env.apiKey}&alt=media`)
    return await request.text()
  }

}
