import { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm';
import { PublicationModel } from '../../../UseCases/Publications/PublicationModel';
import { PublicationRepository } from '../../../UseCases/Publications/PublicationRepositories';
import { GoogleDriveRepository } from '../../../Repositories/DataSources/GoogleDriveRepository';

export default function MarkdownStaticPage({pageName}: {pageName: any}) {
    const [file, setFile] = useState<PublicationModel>()

    useEffect(() => {
        const publicationRepository = new PublicationRepository(new GoogleDriveRepository())
        publicationRepository.fetchPage(pageName)
            .then(setFile)
            .catch(console.log)
    }, [pageName])

    return (
        <div className="static-page">
            <ReactMarkdown children={file?.content} remarkPlugins={[remarkGfm]} />
        </div>
    )
}