import React from 'react';
import AppBar from "@mui/material/AppBar"
import { Box, Button, Container, IconButton,  Menu, MenuItem, Toolbar, Typography } from '@mui/material';
import DoNotDisturbOn from '@mui/icons-material/DoNotDisturbOn';
import MenuIcon from '@mui/icons-material/Menu';
import './NavBar.css';
import { Link } from 'react-router-dom';

const pages = [
    { target: '/project', name: 'Le projet' },
    { target: '/publications', name: 'Publications' },
    { target: '/presse', name: 'Presse' },
    { target: '/contact', name: 'Contact' },
];

export default function NavBar() {
    const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(null);

    const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
        setAnchorElNav(event.currentTarget);
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };

    return (
        <AppBar position="static" className='appBar'>
            <Container maxWidth="xl">
                <Toolbar disableGutters>
                    <DoNotDisturbOn sx={{ display: { xs: 'none', md: 'flex' }, mr: 1, color: '#F00' }} />
                    <Typography
                        variant="h6"
                        noWrap
                        component="a"
                        href='/'
                        sx={{
                            mr: 2,
                            display: { xs: 'none', md: 'flex' },
                            fontFamily: 'Roboto',
                            fontWeight: 700,
                            letterSpacing: '.1rem',
                            color: '#000',
                            textDecoration: 'none',
                        }}
                    >
                        Pas de carrière à Laà-Mondrans
                    </Typography>
                    <DoNotDisturbOn sx={{ display: { xs: 'none', md: 'flex' }, mr: 1, color: '#F00' }} />

                    <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleOpenNavMenu}
                            color="inherit"
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorElNav}
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'left',
                            }}
                            open={Boolean(anchorElNav)}
                            onClose={handleCloseNavMenu}
                            sx={{
                                display: { xs: 'block', md: 'none' },
                            }}
                        >
                            {pages.map((page, i) => (
                                <MenuItem key={i} onClick={handleCloseNavMenu}>
                                    <Typography textAlign="center">{page.name}</Typography>
                                </MenuItem>
                            ))}
                        </Menu>
                    </Box>
                    <DoNotDisturbOn sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
                    <Typography
                        variant="h5"
                        noWrap
                        component="a"
                        href='/'
                        sx={{
                            mr: 2,
                            display: { xs: 'flex', md: 'none' },
                            flexGrow: 1,
                            fontFamily: 'monospace',
                            fontWeight: 700,
                            letterSpacing: '.1rem',
                            color: 'inherit',
                            textDecoration: 'none',
                        }}
                    >
                        Pas de carrière à Laà-Mondrans
                    </Typography>
                    <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                        {pages.map((page, i) => (
                            <Button
                                key={i}
                                onClick={handleCloseNavMenu}
                                sx={{ my: 2, color: 'black', display: 'block' }}
                            >
                                <Link to={page.target} className='menu-item'>{page.name}</Link>
                            </Button>
                        ))}
                    </Box>

                </Toolbar>
            </Container>
        </AppBar>
    );
}