import { IconButton, InputBase, Link, Paper, } from "@mui/material";
import SendIcon from '@mui/icons-material/Send';
import Grid from "@mui/material/Unstable_Grid2/Grid2";
import React, { useEffect } from "react";
import { createToken } from "../../../Repositories/Services/JWTEncoder";

export default function FooterBar() {


    const header = { "alg": "RS256", "typ": "JWT" }

    const time = Math.round(Date.now() / 1000)
    const exp = time + 3600

    const claims = {
        "iss": "github-io-website-account@pasdecarrierealaamondrans-web.iam.gserviceaccount.com",
        "scope": "https://www.googleapis.com/auth/prediction",
        "aud": "https://oauth2.googleapis.com/token",
        // "exp": exp,
        // "iat": time
        "exp": 1716924524,
        "iat": 1716920924
    }

    console.log("Header: ", JSON.stringify(header))
    console.log("Claims: ", JSON.stringify(claims))

    const token = createToken(header, claims, 'privateKey')
    console.log("Generated token: ", token)

    fetch(`https://oauth2.googleapis.com/token`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: encodeURI(`grant_type=urn:ietf:params:oauth:grant-type:jwt-bearer&assertion=${token}`)
    })

    useEffect(() => {
       
    })

    return (
        <div className="footerBar">
            <Grid container spacing={2}>
                <Grid xs={12} md={4}>
                    <div className="newsletter">
                        <h3>Recevoir des informations</h3>
                        <Paper
                            component="form"
                            sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: 400 }}
                        >
                            <InputBase
                                sx={{ ml: 1, flex: 1 }}
                                placeholder="Saisissez votre email"
                                inputProps={{ 'aria-label': 'saisissez votre email' }}
                            />
                            <IconButton type="button" sx={{ p: '10px' }} aria-label="search">
                                <SendIcon />
                            </IconButton>
                        </Paper>
                    </div>

                </Grid>
                <Grid xs={12} md={4}>
                    <div className="contact">
                        <h3>Contactez nous</h3>
                        <p>
                            <Link href="#" color="inherit" underline="none">pasdecarrierealaamondrans@gmail.com</Link>
                        </p>
                        <p>
                            06 88 86 33 88
                        </p>
                    </div>
                </Grid>
                <Grid xs={12} md={4} className="sitemap">
                    <h3>Plan du site</h3>
                    <ul>
                        <li>Accueil</li>
                        <li>Le projet</li>
                        <li>Actualités</li>
                        <li>Presse</li>
                        <li>Contact</li>
                    </ul>
                </Grid>
            </Grid>

            Pas de carrière à Laà-Mondrans - 2024
        </div>
    );
}