import crypto from 'crypto-js';
import Base64 from 'crypto-js/enc-base64';
import Utf8 from 'crypto-js/enc-utf8';

// export function toBase64Encode(data: string) {
//     return btoa(data)
// }

export function base64url(source: any) {
    // Encode in classical base64
    let encodedSource = Base64.stringify(source);

    // Remove padding equal characters
    encodedSource = encodedSource.replace(/=+$/, '');

    // Replace characters according to base64url specifications
    encodedSource = encodedSource.replace(/\+/g, '-');
    encodedSource = encodedSource.replace(/\//g, '_');

    return encodedSource;
}

export function utf8Array(data: object) {
    return Utf8.parse(JSON.stringify(data))
}

export function createToken(header: object, claims: object, key: string) {
    const encodedHeader = base64url(utf8Array(header))
    const encodedData = base64url(utf8Array(claims))
    const token = encodedHeader + '.' + encodedData
    const signature = encryptSignature(token, key)

    return token + '.' + signature
}

export function encryptSignature(data: string, key: string) {
    const encrypt = crypto.HmacSHA256(data, key)
    return base64url(encrypt)
}

